import { Component, h, Prop } from '@stencil/core';
import { Items } from './breadcrumbs.types';
/**
 *
 * @exampleComponent limel-example-breadcrumbs-basic
 */

@Component({
    tag: 'limel-breadcrumbs',
    shadow: true,
    styleUrl: 'breadcrumbs.scss',
})
export class Breadcrumbs {
    @Prop()
    public items: Items[];

    @Prop()
    public isButton: boolean = false;

    public render() {
        // console.log(this.items);

        return (
            <nav aria-label="Breadcrumb">
                <ol>
                    <li>{this.renderStep(this.items)}</li>
                    <li>{this.renderLastStep(this.items)}</li>
                </ol>
            </nav>
        );
    }

    private renderStep = (items) => {
        const allStepsWithoutLast = items.slice(0, -1);

        if (this.isButton) {
            return allStepsWithoutLast.map(this.renderButtons);
        }

        return allStepsWithoutLast.map(this.renderLinks);
    };

    private renderLinks = (items) => {
        return (
            <a href={items.url}>
                {this.renderIcon(items)}
                <span>{items.name}</span>
            </a>
        );
    };

    private renderButtons = (items) => {
        return (
            <limel-button
                icon={items.icon}
                label={items.name}
                // onClick={item.url}
            />
        );
    };

    private renderLastStep = (items) => {
        const lastItem = items.slice(-1);
        console.log(lastItem[0].icon, 'iconlast');

        return [
            <limel-icon name={lastItem[0].icon} size="x-small" />,
            <span>{lastItem[0].name}</span>,
        ];
    };

    private renderIcon = (item: Items) => {
        console.log(item.icon);
        if (!item.icon) {
            return;
        }

        return <limel-icon name={item.icon} size="x-small" />;
    };
}
